
<template>
    <div>
   <div class="my-popups modal right fade show" style="display:block" role="dialog">
    <loader :is-visible="isLoading"></loader>
        <div class="modal d-block " tabindex="-1" role="dialog">
        <div class="modal-dialog doc-setup-slide">
            <div class="modal-content">
                <div class="modal-header mheader-user py-0">
                    <button type="button" class="close" v-on:click="close()">
                        <i aria-hidden="true" class="fa fa-times"></i>
                    </button>
                    <h4 class="modal-title py-3" v-if="popupCondition=='setup'">{{ $t('SetupDocStorage') }}</h4>
                    <h4 class="modal-title py-3" v-if="popupCondition == 'Configuration'">{{ $t('Configuration') }}</h4>
                    <span class="p-actions float-right" v-if="CurrentTab==2 || CurrentTab==4">
                        <a class="p-action-btn text-white disabled" id="btnStorageSpace" v-if="popupCondition=='setup'"
                                    @click="AllocatedSpacePopup" data-toggle="modal" alt="Allocate Space"
                                    title="Allocate Space">
                                    <i class="fa fa-plus"></i><br />{{$t('AllocateSpace')}}
                                </a>
                    </span>
                </div>
                <div class="modal-body bg-white">
                    <section class="container-fluid p-0">
                        <div class="left-menu-tab new_tab">
                            <div class="col-md-12 pb-0 " v-if="popupCondition=='setup'">
                                <div class="row">
                                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0 float-left">
                                        <div class="row">
                                            <div class="col-8 pr-0 font-weight-medium">{{ $t('CompleteYourProfile') }}</div>

                                            <div class="col-4 pl-0 text-right"><small><i
                                                        class="fa fa-check-circle pr-1" v-bind:class="{'text-success':SetupProgresText=='Completed', 'text-muted':SetupProgresText!=='Completed'}"></i>{{SetupProgresText}}</small>
                                            </div>
                                        </div>

                                        <div class="progress smaller my-2">
                                            <div s class="progress-bar progress-bar-primary progress-bar px-0 text-center memoryused"
                                                id="divConsumendStorage" :class="`${progressBarColor}`" :style="`width: ${SetupProgress}%; font-weight:500; font-size:14px`"
                                                :title="SetupProgress">{{ SetupProgress }}%</div>
                                            <!-- <div id="divFreeStorage" class="memoryleft" style="padding: 7px;">
                                </div> -->
                                        </div>
                                        <div class="level_info my-2 d-block float-left w-100" style="height:auto;">
                                            <ul>
                                                <li>
                                                    <b id="divConsumendStorage"><span>Progress {{CurrentTab}} of 4</span></b>
                                                </li>
                                                <!-- <li>
                                        <b id="divFreeStorage" style="color: rgb(40, 167, 69);"><span>99.94%</span></b><span> Available Storage </span>
                                    </li> -->
                                            </ul>
                                        </div>
                                    </div>
                                </div>  
                            </div>
                            <div class="mt-0" id="divscrollsetuptabs" style="max-height: calc(100vh - 2px);" v-if="popupCondition=='setup'">

                                <ul class="nav nav-pills flex-column left-tab responivecustomtab lefttabiconfix newtab_des">

                                     <li class="nav-item FourthScreen" @click="ShowNextTab(1)"><a
                                            class="nav-link left-menu" v-bind:class="{'active':CurrentTab==1}" style="cursor: pointer;"><i class="fa fa-random pr-2 text-muted"
                                                v-if="IsStorageSoucesCopmleted == false" aria-hidden="true"></i><i
                                                class="fa fa-check-circle pr-2 text-success" v-if="IsStorageSoucesCopmleted == true"
                                                aria-hidden="true"></i><span class="font-14">{{$t('ChooseStorage')}}</span></a></li>
                                    <li  class="nav-item SecondScreen" @click="ShowNextTab(2)"><a
                                            class="nav-link left-menu" v-bind:class="{'active':CurrentTab==2}" style="cursor: pointer;"><i
                                                class="fa fa-map-marker pr-2 text-muted" v-if="IsClientStoragesCopmleted == false"
                                                aria-hidden="true"></i><i class="fa fa-check-circle font-18 pr-2 text-success"
                                                v-if="IsClientStoragesCopmleted == true" aria-hidden="true"></i><span class="font-14">{{ $t('ClientStorage') }}</span></a>
                                    </li>
                                    <li  class="nav-item ThirdScreen" @click="ShowNextTab(3)"><a
                                            class="nav-link left-menu" v-bind:class="{'active':CurrentTab==3}" style="cursor: pointer;"><i class="fa fa-random pr-2 text-muted"
                                                v-if="IsStorageManagementsCopmleted == false" aria-hidden="true"></i><i
                                                class="fa fa-check-circle pr-2 text-success" v-if="IsStorageManagementsCopmleted == true"
                                                aria-hidden="true"></i><span class="font-14">{{$t('ManageStorage')}}</span></a>
                                    </li>
                                    <li class="nav-item FirstScreen" @click="ShowNextTab(4)"><a
                                            class="nav-link left-menu" v-bind:class="{'active':CurrentTab==4}" style="cursor: pointer;"><i
                                                class="fa fa-user pr-2 text-muted" v-if="IsUserStoragesCopmleted == false" aria-hidden="true"></i>
                                            <i class="fa fa-check-circle pr-2 text-success" v-if="IsUserStoragesCopmleted == true"
                                                aria-hidden="true"></i><span class="font-14">{{$t('UserStorage')}}</span></a>
                                    </li>

                                </ul>


                            </div>
                            <div class="mt-0" id="divscrollsetuptabs" style="max-height: calc(100vh - 2px);" v-if="popupCondition=='Configuration'">

                                <ul class="nav nav-pills flex-column left-tab responivecustomtab lefttabiconfix newtab_des">

                                    <li @click="ShowNextTab(1)" class="nav-item FirstScreen"><a
                                            class="nav-link left-menu" v-bind:class="{'active':CurrentTab==1}" style="cursor: pointer;"><i
                                                class="fa fa-user pr-2 text-muted"></i>
                                            {{ $t('ApplicationLevelTitle') }}</a></li>

                                    <li @click="ShowNextTab(2)" class="nav-item SecondScreen"><a
                                            class="nav-link left-menu" v-bind:class="{'active':CurrentTab==2}" style="cursor: pointer;"><i
                                                class="fa fa-map-marker font-18 pr-2 text-muted"></i>{{ $t('Clients') }}</a>
                                    </li>

                                    <li @click="ShowNextTab(3)" class="nav-item ThirdScreen"><a
                                            class="nav-link left-menu" v-bind:class="{'active':CurrentTab==3}" style="cursor: pointer;"><i
                                                class="fa fa-random pr-2 text-muted"></i>{{$t('Header_Storage')}}</a></li>
                                </ul>
                            </div>
                        </div>
                        <div class="right-content">
                            <div class="col-md-12 p-0" v-if="popupCondition=='setup'">
                                <div class="tab-content">

                                    <div id="step1" class="tab-pane" v-bind:class="{'active show':CurrentTab==1}">
                                        <section class="page-action w-100">
                                            <div class="container-fluid p-0">
                                                <div class="col-md-12 p-0">
                                                    <ChooseStorage></ChooseStorage>
                                                </div>
                                            </div>
                                        </section>
                                    </div>

                                    <div id="step2" class="tab-pane" v-bind:class="{'active show':CurrentTab==2}">
                                        <section class="page-action w-100">
                                            <div class="container-fluid p-0">
                                                <div class="col-md-12 p-0">

                                                    <div class="col-12 float-left p-0">
                                                        <div class="row">
                                                            <ClientStorageListingTable class="col-12" :z-index="99999" ref="Client" v-if="CurrentTab==2" @FetchDatas="FetchDatas" :type="'client'" :key="componentKey"></ClientStorageListingTable>
                                                            <div class="col-12 mt-3">
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </section>
                                    </div>

                                    <div id="step3" class="tab-pane" v-bind:class="{'active show':CurrentTab==3}">
                                        <section class="page-action w-100">
                                            <div class="container-fluid p-0">
                                                <div class="col-md-12 p-0">
                                                    <StorageManagement v-if="CurrentTab==3" :isVisible="CurrentTab==3"></StorageManagement>
                                                </div>
                                            </div>
                                        </section>
                                    </div>

                                    <div id="step4" class="tab-pane call-detail fade border pb-3" v-bind:class="{'active show':CurrentTab==4}">
                                        <section class="page-action w-100">
                                            <div class="container-fluid p-0">
                                                <div class="col-md-12 p-0">
                                                    <div
                                                        class="col-sm-12 col-md-12 col-lg-12 col-xl-12 prof_control float-left">
                                                    </div>

                                                    <div class="col-12 float-left p-0" >
                                                        <div class="row">
                                                            <UserStorageListingTable class="col-12" ref="User" v-if="CurrentTab==4" :type="'user'" :key="componentKey"></UserStorageListingTable>
                                                            <div class="col-12 mt-3">
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </section>
                                    </div>

                                </div>
                            </div>
                            <Configuration :view="'card'" :CurrentTab="CurrentTab" v-if="popupCondition == 'Configuration'"></Configuration>
                        </div>
                    </section>
                </div>
            </div>
        </div>
        </div>
    </div>
    <AllocateSpacepopup v-if="isAllocatedSpaceOpen==true" @FetchDatas="FetchDatas" :jsonobj="jsonobj" @closeAllocatedSpace="closeAllocatedSpace"></AllocateSpacepopup>    
</div>
</template>

<script>
import DataService from "../../../services/DataService";
import AllocateSpacepopup from "./ModalAllocatedSpace.vue"
import EnableDisableSection from "../../../components/Common/EnableDisableSection.vue";
import ChooseStorage from "../../../components/Common/ChooseStorage.vue";
import Configuration from "../../../components/Common/Configuration.vue";
import GeneralSettings from "../../../components/Common/GeneralSettings.vue";
import ClientStorageListingTable from "../../../components/Common/ClientStorageListingTable.vue";
import UserStorageListingTable from "../../../components/Common/UserStorageListingTable.vue";
import StorageManagement from "../../../components/Common/StorageManagement.vue"
export default {
    components: {
        StorageManagement,
        GeneralSettings,
        UserStorageListingTable,
        ClientStorageListingTable,
        EnableDisableSection,
        Configuration,
        ChooseStorage,
        AllocateSpacepopup
    },
    data: function () {
        const simpleSchema = {
            isValid(value) {
                text: 'required|min:2'
                Number: 'required'
            }
        }
        return {
            ProgressCount: 0,
            ShowIconFirst: false,
            ShowIconSecond: false,
            ShowIconThird: false,
            ShowIconFourth: false,
            componentKey:0,
            isLoading:false,
            Shift: false,
            ImageValue: '',
            UserTitleList: [],
            TimeZoneList: [],
            SetupProgress: 0,
            progressBarColor:'',
            IsStorageManagementsCopmleted: false,
            isAllocatedSpaceOpen:false,
            IsUserStoragesCopmleted: false,
            IsClientStoragesCopmleted: false,
            IsStorageSoucesCopmleted: true,
            CurrentTab:this.tab,
            SizeDocStorage: null,
            RetentionValue: null,
            FileTypeVaule: "",
            SizeConfigDataId: null,
            RetentionConfigDataId: null,
            FileCongfigDataId: null,
            jsonobj:[],
            SelectedValue: [],
            FileTypeValues: '',
            Options: [],
            FileTypeData: [{ "label": "jpg", "value": "image/jpg" },
            { "label": "jpeg", "value": "image/jpeg" },
            { "label": "png", "value": "image/png" },
            { "label": "pdf", "value": "application/pdf" },
            { "label": "text", "value": "text/plain" },
            { "label": "xls", "value": "application/vnd.ms-excel" },
            { "label": "xlsx", "value": "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet" },
            { "label": "doc", "value": "application/vnd.openxmlformats-officedocument.wordprocessingml.document" },
            { "label": "docx", "value": "application/docx" },
            { "label": "msword", "value": "application/msword" }
            ],
            jsonobj:[]
        };
    },
    props:{
        popupCondition:{
            type:String,
            required:true
        },
        IsVisible:{
            type: Boolean,
            required: true
        },
    },
    created: function () {
        
        var vm = this;
        vm.isLoading=true;
        vm.CurrentTab=1;
        vm.GetStorageSetupStatusDetail();
        this.getfileTypes();
        this.BindFileTypeDDL();
        vm.isLoading=true;
    },
     computed:{
         SetupProgresText(){
           return this.SetupProgress==0?'Not Started':(this.SetupProgress==100?'Completed':'In Progress');
         }
    },
    mounted: function(){
        var vm= this;
        $('#modalManageView').on('blur', function(){
             this.IsVisible=false;
        });
    },
    watch: {
        IsVisible: {
            handler(val, older) {
                if (val) {
                    this.CurrentTab = 1;
                    this.$parent.showpopup=val;
                    this.IsVisible=val
                }

            }
            // IsVisible: function(value){
            //     this.CurrentTab=1;
            //     this.$parent.showpopup=value;
            // }
        }
    },
    methods: {
        close: function () {
            this.$parent.showpopup=false;
        },
        ShowNextTab: function (tab) {
            var vm= this;
            vm.isLoading=true;
            if(tab==5){
                vm.close();
            }
            vm.CurrentTab= tab;
            if(tab==2){
                //vm.$parent.FetchData();
            }
            vm.isLoading=false;
        },
        GetStorageSetupStatusDetail: function () {
            var vm = this;
            DataService.GetStorageSetupStatusDetail().then(function (response) {
                if (response.data != null) {
                    var res = JSON.parse(response.data);
                    var clientVlaue=JSON.parse(response.data).data[0].AUTO_CLIENT_ACTIVATE;
                    vm.SetupProgress = parseInt(res.data[0].TOTAL_PROGRESS);
                    // if(clientVlaue=='Yes'){
                            if(vm.SetupProgress==25)
                        {
                            vm.progressBarColor="progress-bar progress-bar-striped bg-danger"
                        }
                        else if(vm.SetupProgress==50){
                            vm.progressBarColor="progress-bar progress-bar-striped bg-warning"
                        }
                        else if(vm.SetupProgress==75){
                            vm.progressBarColor="progress-bar progress-bar-striped bg-info"
                        }
                        else if(vm.SetupProgress==100){
                            vm.progressBarColor="progress-bar progress-bar-striped bg-success"
                        }
                    //}
                    // else{
                    //     if(vm.SetupProgress==33)
                    //     {
                    //         vm.progressBarColor="progress-bar progress-bar-striped bg-danger"
                    //     }
                    //     else if(vm.SetupProgress==66){
                    //         vm.progressBarColor="progress-bar progress-bar-striped bg-warning"
                    //     }
                    //     else if(vm.SetupProgress==100){
                    //         vm.progressBarColor="rogress-bar progress-bar-striped bg-success"
                    //     }
                    // }
                    vm.$parent.SetupProgress = parseInt(res.data[0].TOTAL_PROGRESS);
                    vm.IsStorageManagementsCopmleted = parseInt(res.data[0].STORAGE_MANAGEMENT) == 100 ? true : false;
                    vm.IsUserStoragesCopmleted = parseInt(res.data[0].USER_STORAGE) == 100 ? true : false;
                    vm.IsClientStoragesCopmleted = parseInt(res.data[0].CLIENT_STORAGE) == 100 ? true : false;
                    //vm.IsStorageConfigurationsCopmleted =  parseInt(res.data[0].STORAGE_CONFIGURATION)==100?true:false;
                    vm.IsStorageSoucesCopmleted = parseInt(res.data[0].STORAGE_SOURCE) == 100 ? true : false;
                }
            });
        },
        getfileTypes: function () {
            
            var vm = this;
            var params = `moduleName=DocStorage&keyValue=SizePerDoc,RetentionDays,DocStorageAllowedFileTypes`;
            DataService.GetConfigDataValue(params).then((response) => {
                vm.isLoading = true;
                vm.SizeDocStorage = JSON.parse(response.data.ConfigValue)[0].CONFIG_DATA_VALUE;
                vm.SizeConfigDataId = JSON.parse(response.data.ConfigValue)[0].core_config_id;
                vm.RetentionValue = JSON.parse(response.data.ConfigValue)[1].CONFIG_DATA_VALUE;
                vm.RetentionConfigDataId = JSON.parse(response.data.ConfigValue)[1].core_config_id;
                vm.FileTypeVaule = JSON.parse(response.data.ConfigValue)[2].CONFIG_DATA_VALUE;
                vm.FileCongfigDataId = JSON.parse(response.data.ConfigValue)[2].core_config_id;
                vm.SelectedValue = vm.FileTypeVaule.replace(/\'/g, "").split(",");
                vm.isLoading = false;
            });
        },
        closeAllocatedSpace: function(){
            this.isAllocatedSpaceOpen=false;           
        },
        AllocatedSpacePopup: function () {
            debugger
             var vm = this;
             vm.jsonobj=[];
             var id=''
            var values = ''
            $('#tblFileList .chkItems:checked').not("[id^='chkAll']").each(function () {
                 id=$(this).val();
                 values = $(this).closest('tr').find('.maxAllowedStorage').val();
                let objData = {
                    Id: id,
                    value:values
                }
                vm.jsonobj.push(objData)
            })
            vm.isAllocatedSpaceOpen = true;
        },
        BindFileTypeDDL: function () {
            var vm = this;
            vm.Options = []
            vm.FileTypeData.forEach((item, index) => {
                vm.Options.push({
                    value: item.value,
                    label: item.label
                })
            });
        },
        FetchDatas: function(){
            var vm = this;
            vm.componentKey += 1;  
        },
        saveStorageConfiguration: function () {
            var vm = this;
            if (vm.SelectedValue.length == 0) {
                vm.FileTypeValues = "'" + vm.FileTypeData.map(x => x.value).join("','") + "'";
            }
            else {
                //vm.FileTypeValues = "'"+vm.SelectedValue.map(x => x[]).join("','")+"'";
                vm.FileTypeValues = "'" + vm.SelectedValue.join("','") + "'";
            }

            let objStorageConfigData = [{
                DataId: vm.SizeConfigDataId,
                DataValue: vm.SizeDocStorage,
            }, {
                DataId: vm.RetentionConfigDataId,
                DataValue: vm.RetentionValue
            }, {
                DataId: vm.FileCongfigDataId,
                DataValue: vm.FileTypeValues
            }
            ];
            var jsonStorageConfigObj = JSON.stringify(objStorageConfigData);
            var params = `jsonData=${jsonStorageConfigObj}&refCode=DocSetting&moduleName=DocStorage`;
            DataService.ManageDocUserStorageSettingData(params).then((response) => {
                if (response.data == 0) {
                    this.ShowAlert("Storage Configuration Data Updated Successfully");
                    this.getfileTypes();
                }
                else {
                    vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t("Alert"));
                }
            });
        }
    }
}


</script>
