<template>
  <!-- List view -->
  <div class="col-md-12 p-0" v-if="view == 'list'">
    <loader :is-visible="isLoading"></loader>
    <div class="tab-content">
      <div v-if="popupConditions != 'popup'" class="expand-list">
        <a href="javascript:;" class="expand-list-btn justify-content-center align-items-center"><i
            class="icon-solid arrow-solid-left text-white" style="font-size: 9pt"></i></a>
      </div>
      <div class="tab-pane call-detail fade p-0 active show border pb-3">
        <div class="container-fluid">
          <div class="col-md-12 p-0 mt-3">
            <h4 class="float-left">Configuration</h4>
            <!-- <span class="float-right"><em class="fa fa-check-circle mr-2 'text-success'"></em>Completed</span> -->
          </div>

          <div class="col-md-12 p-0 listing">
            <div class="border col-md-12 p-0">
              <div data-bs-toggle="collapse" data-bs-target="#collapseThree" class="col-md-12 setupdoclist p-0">
                <span class="left-icon-m darkbgicon"><em class="fa fa-sliders"></em></span>
                <div class="col-md-9" id="Userlist" @click="redirectLink('General')">
                  <h5 class="card-title font-18 mb-0">General</h5>
                  <p class="card-text font-14">
                    Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed
                    do eiusmod tempor incididunt ut labore et dolore magna
                    aliqua.
                  </p>
                </div>
                <span class="left-icon-m tickarrowposition"><em
                    class="fa fa-chevron-right font-weight-light text-dark font-14"></em></span>
              </div>
              <div id="" class="accordion-collapse collapse border-top-pagi" aria-labelledby="headingTwo"
                data-bs-parent="#accordionExample">
                <div class="accordion-body">
                  <div class="col-12 mt-4 float-left">
                    <div class="row"></div>
                  </div>
                </div>
              </div>
            </div>
            &nbsp;
            <div class="border col-md-12 p-0">
              <div data-bs-toggle="collapse" data-bs-target="#collapseThree" class="col-md-12 setupdoclist p-0">
                <span class="left-icon-m darkbgicon"><em class="fa fa-sliders"></em></span>
                <div class="col-md-9" id="Userlist" @click="redirectLink('Clients')">
                  <h5 class="card-title font-18 mb-0">Clients</h5>
                  <p class="card-text font-14">
                    Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed
                    do eiusmod tempor incididunt ut labore et dolore magna
                    aliqua.
                  </p>
                </div>
                <span class="left-icon-m tickarrowposition"><em
                    class="fa fa-chevron-right font-weight-light text-dark font-14"></em></span>
              </div>
              <div id="" class="accordion-collapse collapse border-top-pagi" aria-labelledby="headingTwo"
                data-bs-parent="#accordionExample">
                <div class="accordion-body">
                  <div class="col-12 mt-4 float-left">
                    <div class="row"></div>
                  </div>
                </div>
              </div>
            </div>
            &nbsp;
            <div class="border col-md-12 p-0">
              <div data-bs-toggle="collapse" data-bs-target="#collapsethree" class="col-md-12 setupdoclist p-0">
                <span class="left-icon-m darkbgicon"><em class="fa fa-cog"></em></span>
                <!-- <div class="col-md-9" id="StorageConfiguration" @click="redirectLink('Storage')"> -->
                <div class="col-md-9" id="StorageConfiguration">
                  <h5 class="card-title font-18 mb-0">Storage</h5>
                  <p class="card-text font-14">
                    Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed
                    do eiusmod tempor incididunt ut labore et dolore magna
                    aliqua.
                  </p>
                </div>
                <!-- <span class="left-icon-m tickposition"><em class="fa fa-check-circle mr-2" v-bind:class="{'text-success':IsStorageConfigurationsCopmleted}"></em></span> -->
                <span class="left-icon-m tickarrowposition"><em
                    class="fa fa-chevron-down font-weight-light text-dark font-14"></em></span>
              </div>
              <div id="collapsethree" class="accordion-collapse collapse border-top-pagi" aria-labelledby="headingTwo"
                data-bs-parent="#accordionExample">
                <div class="accordion-body">
                  <div class="col-12 mt-4 float-left">
                    <div class="row">
                      <div class="col-sm-12 col-md-12 float-left col-lg-6 col-xl-6">
                        <div class="form-group">
                          <label>Size Of Per File In MB For Doc. Storage:<span class="mandatory">*</span></label>
                          <Field name="SizeDocStorage" v-model="SizeDocStorage" :rules="{ required: true }"
                            label="Size DocStorage" v-slot="{ errors }">
                            <input type="number" id="SizeDocStorage" class="form-control" v-model="SizeDocStorage" />
                            <span name="SizeDocStorage" id="SizeDocStorage" class="invalid-feedback d-block">{{
                              errors[0] }}</span>
                          </Field>
                        </div>
                      </div>
                      <div class="col-sm-12 col-md-12 float-left col-lg-6 col-xl-6">
                        <div class="form-group">
                          <label>Retention Day(s):<span class="mandatory">*</span></label>
                          <Field name="RetentionValue" v-model="RetentionValue" :rules="{ required: true }"
                            label="Retention Value" v-slot="{ errors }">
                            <input type="number" id="RetentionValue" class="form-control" v-model="RetentionValue" />
                            <span name="RetentionValue" id="RetentionValue" class="invalid-feedback d-block">{{
                              errors[0] }}</span>
                          </Field>
                        </div>
                      </div>
                      <div class="col-sm-12 col-md-12 float-left col-lg-6 col-xl-6">
                        <div class="form-group">
                          <label>File Type:<span class="mandatory">*</span></label>
                          <Field :rules="{ required: true }" name="'File Type'" v-slot="{ errors }">
                            <Multiselect v-model="SelectedValue" :options="Options" mode="tags" label="label"
                              track-by="value" :close-on-select="true" :searchable="true" :taggable="false"
                              placeholder="All">
                            </Multiselect>
                            <span name="SelectedValue" id="SelectedValue" class="invalid-feedback d-block">{{ errors[0]
                            }}</span>
                          </Field>
                        </div>
                      </div>

                      <div class="col-12 mt-3">
                        <div class="row flex-row-reverse">
                          <div class="col-lg-12 text-right">
                            <button title="Save" @click="saveStorageConfiguration()"
                              class="popupgreenbtn btn btn-success formbtn widthhalf pr-2">
                              <em aria-hidden="true" class="fa fa-save pr-2"></em>
                              Save
                            </button>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            &nbsp;
          </div>
        </div>
      </div>
    </div>
  </div>
  <!-- Card View -->
  <div class="col-md-12 p-0" v-else-if="view == 'card'">
    <loader :is-visible="isLoading"></loader>
    <div class="tab-content">
      <div id="step1" class="tab-pane call-detail fade p-0 border pb-3" v-bind:class="{ 'active show': CurrentTab == 1 }">
        <section class="page-action w-100">
          <div class="container-fluid p-0">
            <div class="col-md-12 p-0 mt-3">
              <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 prof_control float-left" id="ulKeysContainer">
                <div class="container-fluid p-0">
                  <div class="col-md-12 p-0">
                    <div class="row">
                      <Form v-slot="{ values, errors }" class="col-12 p-0" ref="form">
                        <div v-for="(item, allConfigDataIndex) in allConfigData"
                          class="col-sm-12 col-md-12 float-left col-lg-6 col-xl-6"
                          :key="'configdata-1' + allConfigDataIndex">
                          <div
                            v-if="item.CORE_CONFIG_TYPE == 'dropdown' && item.CORE_CONFIG_KEY != 'TimeZone' && item.CORE_CONFIG_KEY != 'Currency'"
                            class="form-group projectConfig">
                            <label>
                              <span> {{ item.CORE_CONFIG_DESC }} </span>
                              <span class="text-danger">*</span>
                              <a href="javascript:;" data-toggle="popoveruserguide" :data-content="item.CORE_CONFIG_DESC">
                                <i class="fa fa-question-circle-o text-popover"></i>
                              </a>
                            </label>
                            <Field :rules="{ 'required': true }" name="item.CORE_CONFIG_DESC" v-slot="{ errors }">
                              <select :keys="item.CORE_CONFIG_ID" v-if="item.CORE_CONFIG_KEY != 'Currency'"
                                class="select form-control" v-model="item.CORE_CONFIG_VALUE"
                                name="EnableLocationTrackingModule">
                                <option v-for="(items, GetoptionIndex) in Getoption(item.CORE_CONFIG_OPTIONS)"
                                  :value="items" :key="GetoptionIndex">
                                  {{ items }}
                                </option>
                              </select>
                              <span name="EnableLocationTrackingModule" id="EnableLocationTrackingModule"
                                class="invalid-feedback d-block">{{ errors[0] }}</span>
                            </Field>
                          </div>
                        </div>
                        <div v-for="(item, allConfigIndex) in allConfigData"
                          class="col-sm-12 col-md-12 float-left col-lg-6 col-xl-6" :key="'configdata-2' + allConfigIndex">
                          <div v-if="item.CORE_CONFIG_TYPE == 'text'" class="form-group projectConfig">
                            <label>
                              <span> {{ item.CORE_CONFIG_DESC }} </span>
                              <span class="text-danger">*</span>
                              <a href="javascript:;" :data-content="item.CORE_CONFIG_DESC" data-toggle="popoveruserguide">
                                <i class="fa fa-question-circle-o text-popover" style="font-size: 14px"></i>
                              </a>
                            </label>
                            <!-- <Field name="LocationTracking" v-model="item.CORE_CONFIG_VALUE"
                                    :rules="{ 'required': true }" name="item.CORE_CONFIG_VALUE"
                                     v-slot="{  errors }"> -->

                            <input :keys="item.CORE_CONFIG_ID" type="text" :name="item.CORE_CONFIG_DESC"
                              :value="item.CORE_CONFIG_VALUE" class="textfield form-control" />
                            <span name="LocationTracking" id="LocationTracking" class="invalid-feedback d-block">
                            </span>
                            <!-- </Field> -->
                          </div>
                        </div>
                        <div class="col-sm-12 col-md-12 float-left col-lg-6 col-xl-6">
                          <div class="form-group projectConfig">
                            <label>
                              <span>Company Time Zone:</span>
                              <span class="text-danger">*</span>
                              <a href="javascript:;" data-toggle="popoveruserguide" data-content="Company Time Zone">
                                <i class="fa fa-question-circle-o text-popover"></i>
                              </a>
                            </label>
                            <Field name="TimeZone" v-model="getTimeZone" :rules="{ 'required': true }" label="TimeZone"
                              v-slot="{ errors }">
                              <select class="select form-control" :keys="timeZoneKeys" :value="getTimeZone">
                                <option v-for="(item, timeZoneIndex) in timeZone" :value="item.timezone_id"
                                  :key="timeZoneIndex">
                                  {{ item.display_name }}
                                </option>
                              </select>
                              <span name="TimeZone" id="TimeZone" class="invalid-feedback d-block">{{ errors[0]
                              }}</span>
                            </Field>
                          </div>
                        </div>
                        <div class="col-sm-12 col-md-12 float-left col-lg-6 col-xl-6">
                          <div class="form-group projectConfig">
                            <label>
                              <span>Company Logo:</span>
                              <span class="text-danger">*</span>
                              <a href="javascript:;" data-toggle="popoveruserguide" data-content="Company Logo">
                                <i class="fa fa-question-circle-o text-popover" style="font-size: 14px"></i>
                              </a>
                            </label>
                            <div class="row">
                              <div class="col-lg-12 col-md-12 mb-1 mt-0 custom-file uploadimg" id="divFiles">

                                <input type="file" name="attach" id="flFile" @change="setFile(values, meta, $event)"
                                  class="form-control" tabindex="-1"
                                  style="position: absolute; clip: rect(0px, 0px, 0px, 0px)" />

                                <div class="bootstrap-filestyle input-group">
                                  <Field :rules="{ required: true }" name="Company Logo" v-slot="{ errors }">

                                    <input type="text" id="CompanyLogoCoreConfigId" class="form-control disabled"
                                      v-model="attachmentDiv" disabled="" />

                                    <span v-if="errors[0]" name="CompanyLogo" id="CompanyLogoId"
                                      class="invalid-feedback">{{ errors[0] }}</span>
                                  </Field>

                                  <div class="input-group-append" style="cursor: pointer">
                                    <span class="group-span-filestyle input-group-btn input-group-text bg-white"
                                      tabindex="0"><label title="Click here to select a file." for="flFile"
                                        class="mb-0 font-weight-normal">Choose File</label></span><span
                                      class="input-group-text border-0 bg-transparent p-0 pl-2"><a
                                        @click="Clearimagevalue" href="javascript:;"
                                        class="round-icon-small btn-danger-light clsattclear" title="Clear"><i
                                          class="fa fa-undo"></i></a><a href="javascript:;"
                                        class="round-icon-small btn-danger-light clsattremove d-none" title="Remove"><i
                                          class="fa fa-times"></i></a></span>
                                  </div>
                                </div>
                                <span id="spnFile" class="field-validation-valid" data-valmsg-for="FileSrc"></span>
                              </div>
                              <div class="col-lg-5 col-md-12 mt-0 position-relative displayimg"
                                style="max-width: 300px; min-width: 300px">
                                <div class="float-left" style="border: 1px solid #f2f2f2; padding: 2px 14px">
                                  <img id="CompanyLogo" :src="blobUrl" key="29" width="200" style="max-height: 70px"
                                    alt="Image" />
                                </div>

                                <div class="float-left">
                                  <a href="javascript:;" @click="ClearImage" class="remove round-icon-small btn-dark">
                                    <i class="fa fa-times" title="Remove" alt="Remove"></i>
                                  </a>
                                </div>
                              </div>
                            </div>
                          </div>
                        </div>
                        <div class="col-sm-12 col-md-12 float-left col-lg-6 col-xl-6">
                          <div class="form-group projectConfig">
                            <label>
                              <span>Currency:</span>
                              <span class="text-danger">*</span>
                              <a href="javascript:;" data-toggle="popoveruserguide" data-content="Currency">
                                <i class="fa fa-question-circle-o text-popover"></i>
                              </a>
                            </label>
                            <Field :rules="{ 'required': true }" name="Currency Value" v-slot="{ errors }">

                              <select class="select form-control" :keys="getCurrencyValueCoreConfigId"
                                v-model="getCurrencyValue" :value="getCurrencyValue">
                                <option v-for="(item, currencyIndex) in currency" :value="item.CurrencyId"
                                  :key="currencyIndex">
                                  {{ item.Currency }}
                                </option>
                              </select>
                              <span name="Currency" id="Currency" class="invalid-feedback d-block">{{ errors[0]
                              }}</span>
                            </Field>
                          </div>
                        </div>
                        <!-- <div v-for="item in allConfigData" id="radios" type="radiobutton" :keys="item.CORE_CONFIG_ID"
                          class="col-sm-12 col-md-12 float-left col-lg-6 col-xl-6">
                          <label v-if="item.CORE_CONFIG_TYPE == 'radiobutton'"
                            for="project">{{ item.CORE_CONFIG_DESC }}</label>
                          <span v-if="item.CORE_CONFIG_TYPE == 'radiobutton'"  class="text-danger">*</span>
                          <a v-if="item.CORE_CONFIG_TYPE == 'radiobutton'" href="javascript:;"
                            data-toggle="popoveruserguide">
                            <i v-if="item.CORE_CONFIG_TYPE == 'radiobutton'"
                              class="fa fa-question-circle-o text-popover"></i> </a><br />
                              <div>
                                
                              </div>
                          <span id="spn" :keys="item.CORE_CONFIG_ID" v-if="item.CORE_CONFIG_TYPE == 'radiobutton'"
                            v-for="items in Getoption(item.CORE_CONFIG_OPTIONS)">
                            <input :keys="item.CORE_CONFIG_ID" :name="item.CORE_CONFIG_DESC" type="radio" :value="items"
                              v-model="item.CORE_CONFIG_VALUE" />
                            <span>{{ items }}</span>
                          </span>
                        </div> -->


                        <div class="col-sm-12 col-md-12 float-left col-lg-6 col-xl-6" id="radios" type="radiobutton"
                          v-for="(item, index) in allConfigData">
                          <label v-if="item.CORE_CONFIG_TYPE == 'radiobutton'">
                            <span> {{ item.CORE_CONFIG_DESC }} </span>
                            <span class="text-danger">*</span>
                            <a href="javascript:;" data-toggle="popoveruserguide" :data-content="item.CORE_CONFIG_DESC"
                              data-original-title="" title="">
                              <i class="fa fa-question-circle-o text-popover" style="font-size: 14px;"></i>
                            </a>
                          </label>
                          <div class="rad_value" type="radiobutton" :keys="item.CORE_CONFIG_ID"
                            v-if="item.CORE_CONFIG_TYPE == 'radiobutton'">
                            <label v-for="items in Getoption(item.CORE_CONFIG_OPTIONS)"
                              class="cogfiglbl form-check form-check-inline">
                              <div class="custom-control custom-radio custom-control-inline">
                                <input type="radio" class="custom-control-input" :name="item.CORE_CONFIG_DESC"
                                  v-model="item.CORE_CONFIG_VALUE" :value="items" />
                                <label class="custom-control-label" :for="'rdo_' + index"></label>
                              </div>{{ items == 'no' ? 'No' : 'Yes' }}
                            </label>
                          </div>
                        </div>
                        <div class="col-sm-12 col-md-12 float-left col-lg-12 col-xl-12">
                        <div class="row flex-row-reverse">
                          <div class="col-lg-6 text-right">
                            <a id="hlkSave" @click="SaveConfiguration(values, errors)" issecurityprompt="IsSecurityPrompt"
                              class="btn btn-success formbtn" href="javascript:;" title="Save"><i
                                class="fa fa-save pr-2"></i>Save</a>
                          </div>
                        </div>
                      </div>

                      </Form>
                    </div>

                  </div>
                </div>
              </div>
              <div class="col-12 mt-4 float-left">
                <div class="row">
                  <div class="col-12 mt-3"></div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
      <div v-if="CurrentTab == 2" id="step2" class="tab-pane" v-bind:class="{ 'active show': CurrentTab == 2 }">
        <section class="page-action w-100">
          <div class="container-fluid p-0">
            <div class="col-md-12 p-0">
              <div class="col-12 float-left p-0">
                <div class="row">
                  <div class="col-12">
                    <div class="form-group projectConfig">
                      <label>
                        <span> Auto-Activate Client </span>
                        <span class="text-danger">*</span>
                        <a href="javascript:;" data-toggle="popoveruserguide"
                          data-content=" Auto-Activate the client at the time of Creation." data-original-title=""
                          title="">
                          <i class="fa fa-question-circle-o text-popover" style="font-size: 14px;"></i>
                        </a>
                      </label>
                      <div class="rad_value" type="radiobutton">
                        <label class="cogfiglbl form-check form-check-inline">
                          <div class="custom-control custom-radio custom-control-inline">
                            <input type="radio" name="SendNotificationsToClient" value="Yes"
                              v-model="IsClientAutoActivated" id="rdo_0" class="custom-control-input">
                            <label class="custom-control-label" for="rdo_0"></label>
                          </div>Yes
                        </label>
                        <label class="cogfiglbl form-check form-check-inline">
                          <div class="custom-control custom-radio custom-control-inline">
                            <input type="radio" name="SendNotificationsToClient" value="No"
                              v-model="IsClientAutoActivated" id="rdo_1" class="custom-control-input">
                            <label class="custom-control-label" for="rdo_1">
                            </label>
                          </div>No
                        </label>
                      </div>
                    </div>
                  </div>
                  <div class="col-12 mt-3 p-0">
                    <div class="col-lg-6 text-right float-right">
                      <a class="btn btn-success formbtn" href="javascript:;" title="Save"
                        @click="saveClientConfiguration()"><i class="fa fa-save pr-2"></i>Save</a>
                    </div>
                    <div class="col-lg-6">
                      <div class="py-2"><span class="mandatory">Fields marked with an asterisk (*) are mandatory.</span>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
      <div v-if="CurrentTab == 3" id="step3" class="tab-pane" v-bind:class="{ 'active show': CurrentTab == 3 }">
        <section class="page-action w-100">
          <div class="container-fluid p-0">
            <div class="col-md-12 p-0">
              <div class="accordion-body">
                <div class="col-12 p-0 float-left">
                  <Form v-slot="{ valid, errors }" ref="Storageform">
                    <div class="row">
                      <div class="col-sm-12 col-md-12 float-left col-lg-6 col-xl-6">
                        <div class="form-group">
                          <label>Size Of Per File In MB For Doc. Storage:<span class="mandatory">*</span></label>
                          <Field v-model="SizeDocStorage" :rules="{ required: true }" name="Size DocStorage"
                            v-slot="{ errors }">
                            <input type="number" min="1" id="SizeDocStorage"
                              @keyup="removeSpecialCharacters(SizeDocStorage)" class="form-control"
                              v-model="SizeDocStorage" />
                            <span name="SizeDocStorage" id="SizeDocStorage" class="invalid-feedback d-block">{{ errors[0]
                            }}</span>
                          </Field>
                        </div>
                      </div>
                      <div class="col-sm-12 col-md-12 float-left col-lg-6 col-xl-6">
                        <div class="form-group">
                          <label>Retention Day(s):<span class="mandatory">*</span></label>
                          <Field v-model="RetentionValue" :rules="{ required: true }" name="Retention Value"
                            v-slot="{ errors }">
                            <input type="number" id="RetentionValue" min="1" class="form-control"
                              v-model="RetentionValue" />
                            <span name="RetentionValue" id="RetentionValue" class="invalid-feedback d-block">{{ errors[0]
                            }}</span>
                          </Field>
                        </div>
                      </div>
                      <div class="col-sm-12 col-md-12 float-left col-lg-6 col-xl-6">
                        <div class="form-group">
                          <label>File Type:<span class="mandatory">*</span></label>
                          <Field :rules="{ required: true }" name="File Type" v-slot="{ errors }">
                            <Multiselect v-model="SelectedValue" :multiple="true" :options="Options" mode="tags" 
                               :close-on-select="false"
                              :searchable="true" :taggable="true" label="label" track-by="value"  v-bind="field"
                              placeholder="Select">
                            </Multiselect>
                            <span name="SelectedValue" id="SelectedValue" class="invalid-feedback d-block">{{ errors[0]
                            }}</span>
                          </Field>
                        </div>
                      </div>

                      <div class="col-sm-12 col-md-12 float-left col-lg-6 col-xl-6">
                        <div class="form-group"><label>Default Storage Allocated Per User (MB) :<span
                              class="mandatory">*</span></label>
                          <Field :rules="{ 'required': true }" name="Storage Account" v-slot="{ errors }">
                            <input type="number" id="StorageAllocatedPerUser" v-model="StorageAllocatedPerUser"
                              class="form-control" />
                            <span name="StorageAllocatedPerUser" id="StorageAllocatedPerUser" v-if="errors[0]"
                              class="invalid-feedback d-block">{{ errors[0] }}</span>
                          </Field>
                        </div>
                      </div>
                      <div class="col-sm-12 col-md-12 float-left col-lg-6 col-xl-6">
                        <div class="form-group"><label>Default Storage Allocated Per Client (MB):<span
                              class="mandatory">*</span></label>
                          <Field :rules="{ 'required': true }" name="Storage Account" v-slot="{ errors }">
                            <input type="number" id="StorageAllocatedPerClient" v-model="StorageAllocatedPerClient"
                              class="form-control" />
                            <span name="StorageAllocatedPerClient" id="StorageAllocatedPerClient" v-if="errors[0]"
                              class="invalid-feedback d-block">{{ errors[0] }}</span>
                          </Field>
                        </div>
                      </div>
                      <div class="col-12 mt-3">
                        <div class="row flex-row-reverse">
                          <div class="col-lg-12 text-right">
                            <button title="Save" @click="saveStorageConfiguration()"
                              class="popupgreenbtn btn btn-success formbtn widthhalf pr-2">
                              <em aria-hidden="true" class="fa fa-save pr-2"></em>
                              Save
                            </button>
                          </div>
                        </div>
                      </div>
                    </div>
                  </Form>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    </div>
  </div>
</template>

<script>
import DataService from "../../services/DataService";
export default {
  data() {
    return {
      SizeDocStorage: null,
      RetentionValue: null,
      FileTypeVaule: "",
      SizeConfigDataId: null,
      RetentionConfigDataId: null,
      FileCongfigDataId: null,
      userStrogaeConfigId: null,
      clientStorageConfigId: null,
      SelectedValue: [],
      FileTypeValues: "",
      Options: [],
      FileTypeData: [],
      IsClientAutoActivated: '',
      ClientAutoActivationConfigId: null,
      lockRecord: [],
      roundingOff: [],
      timeFormat: [],
      defaultPageSize: [],
      EnablelocationTracking: [],
      locationTracking: null,
      timeZone: [],
      currency: [],
      getTimeZone: 0,
      getLocationTrackInterval: 0,
      paymentLink: '',
      getCurrencyValue: 0,
      getTimeFormatValue: 0,
      getTalygenTips: '',
      getPastRecordValue: 0,
      newlyAddUserValue: '',
      locationTrackingCoreConfigId: 0,
      defaultPageSizeValue: 0,
      newlyAddUserValueoption: [],
      talygenTipsOption: [],
      attachmentDiv: '',
      blobUrl: '',
      imgPath: '',
      containerName: '',
      arrayGetFileDetails: [],
      UserType: '',
      fileName: '',
      allConfigData: [],
      objData: [],
      timeZoneKeys: 0,
      allConfigObject: [],
      isLoading: false,
      defaultCompanyLogo: null,
      StorageAllocatedPerUser: null,
      StorageAllocatedPerClient: null,
      jsonObj: [],
      getCurrencyValueCoreConfigId: null,
      SelectValue: []
    };
  },
  props: {
    popupConditions: {
      type: String,
      required: false,
    },
    view: {
      type: String,
      required: true,
    },
    CurrentTab: {
      type: Number,
      required: true
    }
  },
  computed: {},
  created: function () {

    this.BindFileTypeDDL();
    var vm = this;
    setTimeout(function () {

      vm.getfileTypes();
    }, 3000);

    this.getClientConfigValue();
    this.GetConfigurationData();
    this.GetTimeZone();
    this.GetCurrency();
    this.containerName = this.GetUserInfo.ContainerName
    this.UserType = this.GetUserInfo.UserType
    // setTimeout("ShowPopOver()", 500);
  },
  watch: function () {
    this.isLoading = true;
    var vm = this;
    vm.BindFileTypeDDL();
    setTimeout(function () {

      vm.getfileTypes();
    }, 3000);

    this.getClientConfigValue();
    this.GetConfigurationData();
    this.GetTimeZone();
    this.GetCurrency();
    this.isLoading = false;
    this.containerName = this.GetUserInfo.ContainerName
    this.UserType = this.GetUserInfo.UserType
  },
  methods: {
    saveStorageConfiguration: function () {
      debugger
      var vm = this;
      if (vm.SelectedValue.length == 0) {
        vm.FileTypeValues =
          "'" + vm.FileTypeData.map((x) => x.value).join("','") + "'";
      } else {
        vm.SelectedValue.forEach((item, index) => {
          vm.SelectValue.push(item.value);
        })
        vm.FileTypeValues = vm.SelectValue.join(',');
      }

      let objStorageConfigData = [
        {
          configData: [
            {
              DataId: vm.SizeConfigDataId,
              DataValue: vm.SizeDocStorage,
            },
            {
              DataId: vm.RetentionConfigDataId,
              DataValue: vm.RetentionValue,
            },
            {
              DataId: vm.FileCongfigDataId,
              DataValue: vm.FileTypeValues,
            },
            {
              DataId: vm.userStrogaeConfigId,
              DataValue: vm.StorageAllocatedPerUser,
            },
            {
              DataId: vm.clientStorageConfigId,
              DataValue: vm.StorageAllocatedPerClient,
            },
          ]
        },
        {
          conditionData: {
            refCode: 'DocSetting',
            moduleName: 'DocStorage',
          }
        }
      ];
      var ParentData = JSON.stringify(objStorageConfigData);
      if (parseInt(vm.SizeDocStorage) > parseInt(vm.StorageAllocatedPerUser) || parseInt(vm.SizeDocStorage) > parseInt(vm.StorageAllocatedPerClient)) {
        $('#SizeDocStorage').addClass('is-invalid');
        vm.ShowAlert(vm.$t("Size of per file cannot be more than " + vm.StorageAllocatedPerUser), "failure", true);
      }
      else {
        $('#SizeDocStorage').removeClass('is-invalid');
        vm.$refs.Storageform.validate().then((result) => {
          if (result) {
            DataService.ManageDocUserStorageSettingData(JSON.stringify(ParentData)).then((response) => {
              if (response.data == 0) {
                vm.ShowAlert("Storage Configuration Data Updated Successfully");
                vm.getfileTypes();
              } else {
                vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t("Alert")
                );
              }
            });
          }
        });
      }
    },
    removeSpecialCharacters(charactersString) {
      if (charactersString == 0) {
        this.SizeDocStorage = parseInt(charactersString.replace(0, 1));
      }
    },
    BindFileTypeDDL: function () {
      debugger
      var vm = this;
      vm.Options = [];
      var params = `moduleName=DocStorage`;
      DataService.GetFileType(params).then((response) => {
        vm.FileTypeData = response.data;
        vm.FileTypeData.forEach((item, index) => {
          vm.Options.push({
            value: item.MimeTypes,
            label: item.FileType,
          });
        });
      })
    },
    getfileTypes: function () {
      debugger
      var vm = this;
      var params = `moduleName=DocStorage&keyValue=SizePerDoc,RetentionDays,DocStorageAllowedFileTypes,DefaultUserSpaceAllocation,DefaultClientSpaceAllocation`;
      DataService.GetConfigDataValue(params).then((response) => {
        vm.isLoading = true;
        var ConfigList = JSON.parse(response.data.ConfigValue);
        vm.SizeDocStorage = ConfigList.filter(v => v.CONFIG_DATA_KEY === 'SizePerDoc')[0].CONFIG_DATA_VALUE;
        vm.SizeConfigDataId = ConfigList.filter(v => v.CONFIG_DATA_KEY === 'SizePerDoc')[0].core_config_id;
        vm.RetentionValue = ConfigList.filter(v => v.CONFIG_DATA_KEY === 'RetentionDays')[0].CONFIG_DATA_VALUE;
        vm.RetentionConfigDataId = ConfigList.filter(v => v.CONFIG_DATA_KEY === 'RetentionDays')[0].core_config_id;
        vm.FileTypeVaule = ConfigList.filter(v => v.CONFIG_DATA_KEY === 'DocStorageAllowedFileTypes')[0].CONFIG_DATA_VALUE;
        vm.FileCongfigDataId = ConfigList.filter(v => v.CONFIG_DATA_KEY === 'DocStorageAllowedFileTypes')[0].core_config_id;
        vm.StorageAllocatedPerUser = ConfigList.filter(v => v.CONFIG_DATA_KEY === 'DefaultUserSpaceAllocation')[0].CONFIG_DATA_VALUE;
        vm.userStrogaeConfigId = ConfigList.filter(v => v.CONFIG_DATA_KEY === 'DefaultUserSpaceAllocation')[0].core_config_id;
        vm.StorageAllocatedPerClient = ConfigList.filter(v => v.CONFIG_DATA_KEY === 'DefaultClientSpaceAllocation')[0].CONFIG_DATA_VALUE;
        vm.clientStorageConfigId = ConfigList.filter(v => v.CONFIG_DATA_KEY === 'DefaultClientSpaceAllocation')[0].core_config_id;
        // vm.SelectedValue = vm.FileTypeVaule.replace(/\'/g, "").split(',');
        var SelectedValue = vm.FileTypeVaule.replace(/\'/g, "").split(',');
        if (SelectedValue != null) {
          var selectedValues = vm.Options.filter((x) => { return SelectedValue.some((y) => { return x.value.indexOf(y) != -1 }) });
          vm.SelectedValue = selectedValues.map(x => x);
        }
        else {
          vm.SelectedValue = vm.Options.map(x => x.value);
        }
        vm.isLoading = false;
      });
    },

    redirectLink: function (params) {
      var envLink = process.env;
      if (params == "General") {
        window.open(envLink.VITE_General);
        return false;
      } else if (params == "Storage") {
        window.open(envLink.VITE_Storage);
        return false;
      } else if (params == "Clients") {
        window.open(envLink.VITE_Clients);
        return false;
      }
    },
    getClientConfigValue: function () {
      var vm = this;
      var params = `moduleName=Client&keyValue=SendNotificationsToClient`;
      DataService.GetConfigDataValue(params).then((response) => {
        vm.isLoading = true;
        vm.IsClientAutoActivated = JSON.parse(response.data.ConfigValue)[0].CONFIG_DATA_VALUE;
        vm.ClientAutoActivationConfigId = JSON.parse(response.data.ConfigValue)[0].core_config_id;
        vm.isLoading = false;
      });
    },
    saveClientConfiguration: function () {
      var vm = this;
      let objStorageConfigData = [
        {
          configData: [
            {
              DataId: vm.ClientAutoActivationConfigId,
              DataValue: vm.IsClientAutoActivated == 'Yes' ? 'Yes' : 'No',
            },
          ]
        },

        {
          conditionData: {
            refCode: 'DocSetting',
            moduleName: 'DocStorage',
          }

        }
      ];
      var jsonStorageConfigObj = JSON.stringify(objStorageConfigData);
      //var params = `jsonData=${jsonStorageConfigObj}&refCode=DocSetting&moduleName=DocStorage`;
      DataService.ManageDocUserStorageSettingData(JSON.stringify(jsonStorageConfigObj)).then((response) => {
        if (response.data == 0) {
          this.ShowAlert("Client Configuration Data Updated Successfully");
          this.getClientConfigValue();
        }
        else {
          vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t("Alert"));
        }
      });
    },
    GetConfigurationData: function () {

      var vm = this;
      var myObj = `moduleId=1`
      DataService.GetConfigurationData(myObj).then(function (response) {
        vm.allConfigData = response.data.StorageKeys;
        vm.EnablelocationTracking = response.data.StorageKeys[0].CORE_CONFIG_OPTIONS.split("^");
        vm.locationTrackingCoreConfigId = response.data.StorageKeys[1].CORE_CONFIG_ID;
        vm.getTimeZone = response.data.StorageKeys[2].CORE_CONFIG_VALUE;
        vm.getTimeZoneCoreConfigId = response.data.StorageKeys[2].CORE_CONFIG_ID;
        vm.defaultPageSize = response.data.StorageKeys[3].CORE_CONFIG_OPTIONS.split("^");
        vm.getCurrencyValue = response.data.StorageKeys[7].CORE_CONFIG_VALUE;
        vm.getCurrencyValueCoreConfigId = response.data.StorageKeys[7].CORE_CONFIG_ID;
        vm.getTalygenTipsCoreConfigId = response.data.StorageKeys[9].CORE_CONFIG_ID;
        vm.lockRecord = response.data.StorageKeys[10].CORE_CONFIG_OPTIONS.split("^");
        vm.lockRecordCoreConfigId = response.data.StorageKeys[10].CORE_CONFIG_ID;
        vm.getPastRecordValue = response.data.StorageKeys[10].CORE_CONFIG_VALUE;
        vm.newlyAddUserValueoption = response.data.StorageKeys[11].CORE_CONFIG_OPTIONS.split("^");
        vm.newlyAddUserValue = response.data.StorageKeys[11].CORE_CONFIG_VALUE;
        vm.newlyAddUserValueCoreConfigId = response.data.StorageKeys[11].CORE_CONFIG_ID;
        vm.talygenTipsOption = response.data.StorageKeys[9].CORE_CONFIG_OPTIONS.split("^");
        vm.imgPath = response.data.StorageKeys[5].CORE_CONFIG_VALUE;
        vm.CompanyLogoCoreConfigId = response.data.StorageKeys[5].CORE_CONFIG_ID;
        vm.timeZoneKeys = response.data.StorageKeys[2].CORE_CONFIG_ID;
        vm.GetBlobUrl()
      })
    },
    GetTimeZone: function () {
      var vm = this;
      DataService.GetTimeZone().then(function (response) {
        vm.timeZone = response.data;
      })
    },
    GetCurrency: function () {

      var vm = this;
      DataService.GetCurrency().then(function (response) {
        vm.currency = response.data;
      })
    },
    SaveConfiguration: function (values, error) {

      var vm = this;
      vm.objData = [];
      let imgObj = {};
      var textValues = $('div#ulKeysContainer input:text,div#ulKeysContainer select,div#ulKeysContainer textarea,div#ulKeysContainer input:password'); //get all textboxes,textarea and drop down list from config list
      //var checkBoxValues = $('div#radios').find("input[type='radio']"); //get all div contains checkboxes from config list
      var checkBoxValues = $('div#ulKeysContainer div[type="checkbox"], div#ulKeysContainer div[type="radiobutton"]');
      var imageControl = $('#flFile').val();
      var splitimage = imageControl.split('\\')[2];
      if (splitimage != undefined && vm.defaultCompanyLogo == null) {
        imgObj = {
          DataId: vm.CompanyLogoCoreConfigId,
          DataValue: splitimage == undefined ? this.imgPath : splitimage
        }
      }
      else if (vm.defaultCompanyLogo != null) {
        imgObj = {
          DataId: vm.CompanyLogoCoreConfigId,
          DataValue: 'COMPANY-LOGO.JPG'
        }
      }
      vm.objData.push(imgObj);
      for (var i = 0; i < textValues.length; i++) {
        if ($(textValues[i]).attr('keys') != undefined) {
          var textValue = $(textValues[i]).val().trim();
          if ($(textValues[i]).val().trim() == '') {
            if ($(textValues[i]).attr("class").indexOf("notrequired") == -1) {
              $(textValues[i]).addClass('is-invalid');
              $(textValues[i] + 'span#LocationTracking').html('Required');
            }

          }
          else {
            $(textValues[i]).removeClass('is-invalid');
          }
          let objStorageConfigData = {
            DataId: $(textValues[i]).attr('keys'),
            DataValue: textValue
          }
          vm.objData.push(objStorageConfigData);
        }
      }
      for (var i = 0; i < checkBoxValues.length; i++) {
        var getSelectedvalues = '';
        if ($(checkBoxValues[i]).attr('keys') != undefined && $(checkBoxValues[i]).attr('keys') != 'undefined') {
          if ($(checkBoxValues[i]).attr('type') == 'radiobutton') {
            var combinedName = ("config" + i);
            //getSelectedvalues = $("input[name='"+combinedName+"']:checked").val();
            getSelectedvalues = $(checkBoxValues[i]).find('input:radio:checked').val();
          }
          let radioConfigDatas = {
            DataId: $(checkBoxValues[i]).attr('keys'),
            DataValue: getSelectedvalues
          }
          vm.objData.push(radioConfigDatas);
          let jsonObject = vm.objData.map(JSON.stringify);
          let uniqueSet = new Set(jsonObject);
          vm.allConfigObject = Array.from(uniqueSet).map(JSON.parse);
        }
      }
      var error = $('.is-invalid');
      const formdata = new FormData();
      formdata.append("files", vm.arrayGetFileDetails)
      if (splitimage != undefined) {
        DataService.UploadFile2(formdata).then((response) => {
          debugger
        })
      }
      let userset = [{
        refCode: 'DocSetting',
        moduleName: 'DocStorage',
      }]
      var configData = "configData"
      var conditionData = "conditionData"
      var params = "[{" + JSON.stringify(configData) + ":" + JSON.stringify(vm.allConfigObject) + ", " + JSON.stringify(conditionData) + ":" + JSON.stringify(userset) + "}]"
      var jsonStorageConfigObj = JSON.stringify(vm.allConfigObject);
      //var params = `jsonData=${jsonStorageConfigObj}&refCode=DocSetting&moduleName=DocStorage`;
      if ($(error).length <= 0) {
        DataService.ManageDocUserStorageSettingData(JSON.stringify(params)).then((response) => {
          vm.isLoading = true;
          if (response.data == 0) {
            vm.ShowAlert("Record(s) has been successfully saved.");
            vm.attachmentDiv = '';
            vm.isLoading = false;
            vm.GetConfigurationData();
          }
          else {
            vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t("Alert"));
            vm.isLoading = false;
          }
        });
      }
      else {
        vm.ShowAlert(vm.$t("PleaseCorrectHighlightedErrors"), "failure", true);
      }
    },
    setFile: function (values, meta, e) {
      this.attachmentDiv = e.target.files[0].name;
      this.fileName = e.target.files[0];
      this.arrayGetFileDetails = e.target.files[0];
    },
    GetBlobUrl: function () {

      DataService.GetBlobUrl().then((response) => {

        var url = response.data[0].CONFIG_DATA_VALUE;
        var usertype = this.UserType;
        if (this.imgPath == "COMPANY-LOGO.JPG") {
          this.blobUrl = "/Content/images/DefaultImages/company-logo.jpg"
        }
        if (usertype == "CA" && this.imgPath != "COMPANY-LOGO.JPG") {
          this.blobUrl = url + '/' + this.containerName + '/' + 'Upload' + '/' + 'CompanyLogo' + '/' + this.imgPath
        }
      })
    },
    Getoption: function (params) {
      params = params == null ? '' : params;
      var options = [];
      $.each(params.split('^'), function (i, v) {
        options.push(v);
      });
      return options;
    },
    ClearImage: function () {
      this.blobUrl = "/Content/images/DefaultImages/company-logo.jpg"
      this.defaultCompanyLogo = 'COMPANY-LOGO.JPG'
    },
    Clearimagevalue: function () {
      var vm = this;
      vm.attachmentDiv = '';
    }
  }
};
</script>
